    printDate(){
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth()+1
      var day = date.getDate();
      if(month < 10){
          month = "0" + month;
      }
      if(day < 10){
          day = "0" + day;
      }
      var today = year+"년 "+month+"월 "+day+"일 ";
      return today;
    },
